jest.mock('@react-native-community/push-notification-ios', () => ({ presentLocalNotification: jest.fn() }));
jest.mock('@react-native-community/progress-bar-android', () => 'ProgressBarAndroid');
jest.mock('expo-modules-core', () => ({
  ...jest.requireActual('expo-modules-core'),
  NativeModulesProxy: new Proxy({}, {
    get(target, prop) {
      if (prop === 'ExpoLocalization') {
        return { locale: 'en-US' };
      }
      return {};
    }
  }),
  EventEmitter: jest.fn(() => ({ addListener: jest.fn(), removeListeners: jest.fn() })),
}));

// Polyfills
if (typeof setImmediate === 'undefined') {
  global.setImmediate = (fn, ...args) => setTimeout(fn, 0, ...args);
}
if (typeof clearImmediate === 'undefined') {
  global.clearImmediate = (id) => clearTimeout(id);
}

// Suppress warnings
const originalConsole = { ...console };
global.console = {
  ...console,
  error: jest.fn((message) => {
    if (typeof message === 'string' && message.includes('was not wrapped in act')) return;
    originalConsole.error(message);
  }),
  warn: jest.fn((message) => {
    const msg = typeof message === 'string' ? message : '';
    const ignore = [
      'Warning: An update to', 'was not wrapped in act', 'has been extracted',
      'new NativeEventEmitter()', 'PushNotificationIOS', 'Clipboard', 'ProgressBarAndroid'
    ].some((t) => msg.includes(t));
    if (ignore) return;
    originalConsole.warn(message);
  }),
};